
body {
    margin: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    font-size: 12px;
}
#mapContainer {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
#pos {
    background-color: #fff;
    padding-left: 10px;
    padding-right: 10px;
    position: absolute;
    font-size: 12px;
    left: 10px;
    top: 30px;
    border-radius: 3px;
    line-height: 30px;
    border: 1px solid #ccc;
}
#pos input {
    border: 1px solid #ddd;
    height: 23px;
    border-radius: 3px;
    outline: none;
}
#pos label {
    min-width: 100px;
    display: inline-block;
    text-align: left;
}
#result1 {
    max-height: 300px;
}



